<template>
  <div>
    <h3>Trade assets</h3>
    <br/>
    <select v-model="Selected">
      <option disabled='disabled' value>Select asset</option>
      <option v-for='asset in Object.keys (assets)' v-bind:key='asset.id'>
        {{asset}}
      </option>
    </select>

    <input 
    type='number' 
    v-model='tradedAmount' 
    placeholder="0$"
    step="100"
    id='tradeInput'
    min=0/>

    <br/>
    <br/>
    <button 
    style="background-color: green"
    v-on:click='buy'>
      Buy
    </button>
    &nbsp;
    <button 
    style="background-color: red"
    v-on:click='sell'>
      Sell
    </button>

  </div>
</template>

<script>
export default {
  name: 'BuySell',
  props: [
    'assets',
    'USD'
  ],
  data () {
    return {
      Selected: '',
      tradedAmount: ''
    }
  },
  methods: {
    buy () {
      if (Number (this.tradedAmount) > this.USD.amount) {
        alert ('Insufficient funds.');
      } else if (Number (this.tradedAmount) < 0) {
        alert ('Insert a positive value.');
      } else {
        let findAsset = this.assets [this.Selected];
        findAsset.value += Number (this.tradedAmount);
        localStorage [this.Selected] = findAsset.value;
        this.USD.amount -= Number (this.tradedAmount);
        localStorage.USD = this.USD.amount;
        this.tradedAmount = '';
      }
    },
    sell () {
      let findAsset = this.assets [this.Selected];
      if (Number (this.tradedAmount) >  findAsset.value) {
        alert ('Insufficient funds.');
      } else if (Number (this.tradedAmount) < 0) {
        alert ('Insert a positive value.');
      } else {
        findAsset.value -= Number (this.tradedAmount);
        localStorage [this.Selected] = findAsset.value;
        this.USD.amount += Number (this.tradedAmount);
        localStorage.USD = this.USD.amount;
        this.tradedAmount = '';
      }
    }
  }
}
</script>

