<template>
  <div>
      <table class='center' style='width: 50%'>
        <tr>
            <th>Price</th>
            <th>Change</th>
            <th>Asset</th> 
            <th>Value</th>
        </tr>
        <tr v-for='asset in assets' v-bind:key='asset.id'>
            <td v-bind:style='{color: applyColor (asset)}'>{{asset.price}} $</td>
            <td v-bind:style='{color: applyColor (asset)}'>{{asset.change}} %</td>
            <td>{{asset.name}}</td>
            <td>{{roundUp (asset.value)}} $</td>
        </tr>
        </table>
  </div>
</template>

<script>

export default {
  name: 'StockState',
  components: {
  },
  props: ['assets'],
  methods: {
    applyColor (asset) {
        if (asset.change>0) {
            return 'green';
        } else {
            return 'red';
        }
    },
    roundUp (number) {
      var parsed = parseFloat (number);
      return parsed.toFixed (2);
    }
  }
}
</script>

